.generic-component__input {
  display: flex;
  border: none;
  outline: none;
  background-color: rgb(0,0,0,0.0);
  font-weight: 900;
  font-size: 54px;
  line-height: 49px;
  color: #333333;
  width: 100%;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

@media screen and (max-width: 700px) {
  .generic-component__input {
    font-size: 30px;
    line-height: 36px;
  }
}

@media screen and (max-width: 400px) {
  .generic-component__input {
    font-size: 22px;
    line-height: 20px;
  }
}
